import React,{createContext} from 'react'
import { Button } from 'antd'

//  theme  contextlight
const ThemeContext = createContext('danger')
//  theme 
function Toolbar() {
  return (
    <div>
      <ThemedButton />
    </div>
  );
}

class  ThemedButton extends React.Component  {
  //  contextType  theme context
  // React  theme Provider
  //  theme  dark
  //  ThemeContext  class  contextType   this.context render 
  static contextType = ThemeContext
  // Context
  constructor(props){
    super(props)
    this.state = {contextType:ThemeContext}
  }

  render(){
    // render this.context
    console.log('this.context:',this.context)
    return (<>
       {/* Context */}
       <div style={{marginBottom:'12px'}}>
        <Button type={this.state.contextType._currentValue}></Button>
       </div>
       {/* ConsumerContext */}
       <ThemeContext.Consumer>{
         value => (<Button type={value}>Consumer</Button>)
         }</ThemeContext.Consumer>
       {/* ThemeContext  class  */}
        <div style={{marginTop:'12px'}}>
         <Button type={this.context}>{this.context}</Button>
        </div>
    </>)
  }
}

const ContextKw = () => {
  return(<>
     <ThemeContext.Provider value="primary">
        <Toolbar></Toolbar>
      </ThemeContext.Provider>
      {/* Provider */}
      <ThemeContext.Consumer>{
        value => <Button type={value}  style={{marginTop:'12px'}}>Context</Button>
      }</ThemeContext.Consumer> 
  </>)}

export default ContextKw